<template>
	<view class="container">
		<!-- 引导添加小程序 -->
		<!-- #ifdef MP-WEIXIN -->
		<view class="guidance-my" v-if="guidanceMy">
			<view class="triangle-top"></view>
			<view @click="popupBoot()" class="padding-sm  flex">
				<view><span @click.stop="setGuidanceMy" class="icon cuIcon-close text-gray"></span></view>
				<view class="flex-twice text-center">添加到我的小程序，<span class="text-bold">红包、优惠券不错过</span></view>
				<view><span class="icon cuIcon-right text-gray"></span></view>
			</view>
		</view>
		<view class="cu-modal" :class="modalName=='guidanceMy'?'show':''">
			<view class="guidance-modal">
				<view class="triangle-top"></view>
				<view class="title bg-red text-xl padding">点<image style="height: 60upx;position: relative;top:0;margin: 0 auto;" mode="heightFix" src="../../static/guidance-white.png"></image>添加小程序</view>
				<view class="list">
					<view class="padding text-left min-title">
						<span class="text-red">1、</span>
						点右上
						<image style="height: 60upx;position: relative;top:0;" mode="heightFix" src="../../static/guidance.png"></image>
						添加到“我的小程序”
					</view>
					<view>
						<image src="../../static/guidance-1.png" style="height: 100upx;" mode="aspectFit"></image>
					</view>
					<view class="padding text-left min-title">
						<span class="text-red">2、</span>
						回到微信首页，向下拉动
					</view>
					<view>
						<image src="../../static/guidance-2.png" style="height: 140upx;" mode="aspectFit"></image>
					</view>
					<view class="padding text-left min-title">
						<span class="text-red">3、</span>
						从“我的小程序”中，进入
					</view>
					<view>
						<image src="../../static/guidance-3.png" style="height: 140upx;" mode="aspectFit"></image>
					</view>
				</view>
				<view @tap="modalName = null" class="guidance-modal-close">
					<view class="cuIcon-roundclose"></view>
				</view>
			</view>
			
		</view>
		<official-account v-if="!wechat"></official-account>
		<!-- #endif -->
		<!-- 头部轮播 -->
		<view class="carousel-section">
			<!-- 标题栏和状态栏占位符 -->
			<!-- 搜索栏 -->
			<view class="search">
				<view class="top_search">
					<view class="search_icon">
						<view class="yticon icon-sousuo"></view>
					</view>
					<input class="search_input" placeholder="搜索公司" v-model="searchInput" @confirm="searchGo"/>
				</view>
			</view>
	
<!-- 			<view class="backtop">
				
			</view> -->
			<view class="titleNview-placing"></view>
			<!-- 背景色区域 -->
			<!-- <view class="titleNview-background" style="backgroundColor:rgb(203, 87, 60)"></view> -->
			<!-- 自定义swiper指示器 -->
	<!-- 		<view class="swiper-dots">
				<text class="num">{{swiperCurrent+1}}</text>
				<text class="sign">/</text>
				<text class="num">{{swiperLength}}</text>
			</view> -->
		</view>
		<!-- 企业列表 -->
		<qiyeInfoLlist :qiye="qiye"></qiyeInfoLlist>
		<tabbar></tabbar>
	</view>
</template>

<script>
import Good from '../../api/good'
import Banner from '../../api/banner'
import tabbar from '../../components/tabbar.vue'
import qiyeInfoLlist from '../../components/qiye_info_list.vue'
import Sell from '../../api/sell_part'
	export default {

		data() {
			return {
				modalName: null,
				wechat: null,
				guidanceMy: false,
				titleNViewBackground: '',
				swiperCurrent: 0,
				swiperLength: 0,
				carouselList: [],
				goodsList: [],
				searchInput:'',
				adData: {},
				ctegory:[],
				   value1: 0,
				      value2: 'a',
					  value3:0,
				      option1: [
				        { text: '全部商品', value: 0 },
				        { text: '新款商品', value: 1 },
				        { text: '活动商品', value: 2 },
				      ],
				      option2: [
				        { text: '默认排序', value: 'a' },
				        { text: '好评排序', value: 'b' },
				        { text: '销量排序', value: 'c' },
				      ],
					  option3: [
					    { text: '全部商品', value: 0 },
					    { text: '新款商品', value: 1 },
					    { text: '活动商品', value: 2 },
					  ],
					  qiye:[
						  
					  ],
					  id:'',
					  name:''
			};
		},
		components:{
			tabbar,
			qiyeInfoLlist
		},
		onLoad(options) {
			this.id=options.id
			console.log(this.id)
			this.name=options.name
			console.log(this.name)
			uni.setNavigationBarTitle({
				title:this.name
			})
			this.loadData()
			// #ifdef MP-WEIXIN 
			this.wechat=uni.getStorageSync('dsshopUserInfo').wechat
			// #endif
			if(!uni.getStorageSync('applyDsshopGuidanceMy')){
				this.guidanceMy = true
			}
			this.getList()
		},
		onShow(){
			getApp().showDsshopCartNumber()
		},
		methods: {
			/**
			 * 请求数据
			 */
			searchGo(){
				var that=this
				Sell.getList({
					type:this.id,
					title:this.searchInput
				},function(res){
					that.qiye=res.data
				})
			},
			getList(){
				var that=this
				Sell.getList({
					type:this.id
				},function(res){
					that.qiye=res.data
				})
			},
			dropdownClick(e){
				console.log(e)
			},
			async loadData() {
				const that = this
				// 轮播
				await Banner.getList({
					limit: 5,
					type: 0,
					sort: '+sort'
				},function(res){
					that.carouselList = res.data
					that.swiperLength = res.data.length
				})
				// 首页广告
				await Banner.getList({
					type: 1,
					limit: 1,
					sort: '+sort'
				},function(res){
					that.adData = res.data[0]
				})
				// 推荐商品
				await Good.getList({
					limit: 10,
					is_recommend: 1,
				},function(res){
					that.goodsList = res.data
				})
				// 推荐分类
				await Good.goodCategory({
					is_recommend: 1
				},function(res){
					that.ctegory = res
				})
			},
			//轮播图切换修改背景色
			swiperChange(e) {
				const index = e.detail.current;
				this.swiperCurrent = index;
			},
			//轮播跳转
			navToWwiperPage(item) {
				console.log(item)
			},
			//详情页
			navToDetailPage(item) {
				//测试数据没有写id，用title代替
				let id = item.id;
				uni.navigateTo({
					url: `/pages/product/product?id=${id}`
				})
			},
			//跳转
			navTo(url){
				if(url){
					uni.navigateTo({
						url
					})  
				}
				
			}, 
			// #ifdef MP-WEIXIN
			//弹出引导页
			popupBoot(){
				this.modalName = 'guidanceMy'
				this.guidanceMy = false
				uni.setStorageSync('applyDsshopGuidanceMy', true)
			},
			// 引导添加小程序
			setGuidanceMy(){
				this.guidanceMy = false
				uni.setStorageSync('applyDsshopGuidanceMy', true)
			},
			// #endif
		},
		// #ifndef MP
		// 标题栏input搜索框点击
		onNavigationBarSearchInputClicked: async function(e) {
			this.$api.msg('点击了搜索框');
		},
		//点击导航栏 buttons 时触发
		onNavigationBarButtonTap(e) {
			const index = e.index;
			if (index === 0) {
				this.$api.msg('点击了扫描');
			} else if (index === 1) {
				// #ifdef APP-PLUS
				const pages = getCurrentPages();
				const page = pages[pages.length - 1];
				const currentWebview = page.$getAppWebview();
				currentWebview.hideTitleNViewButtonRedDot({
					index
				});
				// #endif
				uni.navigateTo({
					url: '/pages/notice/notice'
				})
			}
		}
		// #endif
	}
</script>

